<template>
	<view class="page">
		<view class="header">
			<input type="text" class="title" :value="name"  placeholder="请输入姓名" placeholder-class="title-placeholder"  @input="inputNameChange"/>
			<image src="../../static/icon-18.png" mode="widthFix" class="edit-icon"></image>
		</view>
		<view class="attr">
			<view class="list">
				
				<view class="item">
					<view class="key">部门</view>
					<picker v-if="canEdit" :value="dept" range-key="deptName" :range="deptRange" @change="endDept">{{ deptDisplay }}</picker>
					<text v-if="!canEdit" class="value">{{ deptDisplay }}</text>
				</view>
				<view class="item">
					<view class="key">性别</view>
					<picker v-if="canEdit" :value="sex" :range="sexRange" @change="endSex">{{ sexDisplay }}</picker>
					<text v-if="!canEdit" class="value">{{ sexDisplay }}</text>
				</view>
				
				<view class="item">
					<view class="key">电话</view>
					<input class="value" type="text" :value="tel" placeholder="请输入手机号" @input="inputTelChange" />
				</view>
				
				<view class="item">
					<view class="key">Email</view>
					<input class="value" type="text" :value="email" placeholder="请输入邮箱地址" @input="inputEmailChange" />
				</view>
				
				<view class="item">
					<view class="key">入职日期</view>
					<picker mode="date" :value="hiredate" @change="endHireDate">
						<view class="uni-input">{{ hiredate }}</view>
					</picker>
				</view>
				<view class="item">
					<view class="key">角色</view>
					<picker mode="selector" :value="role" @change="endRole" range-key="roleName" :range="roleRange">
						<view class="uni-input">{{ roleDisplay }}</view>
					</picker>
				</view>
			</view>
		</view>
		
		
		<view>
			<!-- <view>
				<text class="title">姓名</text>
				<input class="value" type="text" :value="name" placeholder="请输入姓名" @input="inputNameChange" />
			</view> -->

			<!-- <view>
				<text class="title">部门</text>
				<picker mode="selector" :value="dept" range-key="deptName" @change="endDept" :range="deptRange">
					<view class="uni-input">{{ deptDisplay }}</view>
				</picker>
			</view> -->

			<!-- <view>
				<text class="title">性别</text>
				<picker mode="selector" :value="sex" @change="endSex" :range="sexRange">
					<view class="uni-input">{{ sexDisplay }}</view>
				</picker>
			</view> -->

			<!-- <view>
				<text class="title">电话</text>
				<input class="value" type="text" :value="tel" placeholder="请输入手机号" @input="inputTelChange" />
			</view> -->

			<!-- <view>
				<text class="title">Email</text>
				<input class="value" type="text" :value="email" placeholder="请输入邮箱地址" @input="inputEmailChange" />
			</view> -->

			<!-- <view>
				<text class="title">入职日期</text>
				<picker mode="date" :value="hiredate" @change="endHireDate">
					<view class="uni-input">{{ hiredate }}</view>
				</picker>
			</view> -->
			<!-- <view>
				<text class="title">角色</text>
				<picker mode="selector" :value="role" @change="endRole" range-key="roleName" :range="roleRange">
					<view class="uni-input">{{ roleDisplay }}</view>
				</picker>
			</view> -->
		</view>
		<button class="btn" @click="save()">保存</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				canEdit: true,
				isEdit: false,
				code: 0,
				name: '',
				tel: '',
				email: '',
				hiredate: '2021-01-01',
				sex: 0,
				sexRange: ['男', '女'],
				sexDisplay: '男',
				dept: 0,
				deptRange: [],
				deptDisplay: '请选择',


				role: 0,
				roleRange: [],
				roleDisplay: '请选择',
				employee: null

			};
		},
		onLoad: function(options) {

			if (options.action === 'edit') {
				this.isEdit = true
				this.code = options.code
				this.getEmployeeDetail()
			} else {
				this.getDeptList()
				this.getRoleList()
			}

		},
		methods: {

			inputNameChange: function(e) {
				this.name = e.detail.value
			},
			inputTelChange: function(e) {
				this.tel = e.detail.value
			},
			inputEmailChange: function(e) {
				this.email = e.detail.value
			},
			endHireDate: function(e) {
				this.hiredate = e.detail.value
			},
			endSex: function(e) {
				this.sex = e.detail.value
				this.sexDisplay = this.sexRange[this.sex]
			},
			endDept: function(e) {
				this.dept = e.detail.value
				this.deptDisplay = this.deptRange[this.dept].deptName
			},
			endRole: function(e) {
				this.role = e.detail.value
				this.roleDisplay = this.roleRange[this.role].roleName
			},

			getDeptList: function() {
				let that = this
				that.ajax(that.url.searchAllDepts, 'GET', {}, function(resp) {
					that.deptRange = resp.data.result
					if (that.isEdit) {
						let deptId = that.employee.deptId
						for (var i = 0; i < that.deptRange.length; i++) {
							let item = that.deptRange[i]
							console.log(`${item.id}---${deptId}`)
							if (item.id === deptId) {
								that.dept = i
								that.deptDisplay = item.deptName
								break;
							}
						};
					}

				})
 
			},
			getRoleList: function() {

				let that = this
				that.ajax(that.url.searchAllRoles, 'GET', {}, function(resp) {
					that.roleRange = resp.data.result
					if (that.isEdit) {
						let role = that.employee.role
						for (var i = 0; i < that.roleRange.length; i++) {

							let item = that.roleRange[i]
							if (role.indexOf(item.id) > 0) {
								that.role = i
								that.roleDisplay = item.roleName
								break;
							}
						};
					}

				})
			},
			getEmployeeDetail: function() {

				let that = this
				that.ajax(that.url.searchEmployee, 'POST', {
					code: that.code
				}, function(resp) {
					that.employee = resp.data.result
					that.name = that.employee.name
					that.tel = that.employee.tel
					that.email = that.employee.email
					that.sexDisplay = that.employee.sex
					that.hiredate = that.employee.hiredate.substring(0, 10)
					that.getDeptList()
					that.getRoleList()

				})
			},
			save: function() {

				var that = this
				console.log(that.name)
				console.log(that.deptRange[that.dept].deptId)
				if (that.name.length == 0) {

					uni.showToast({
						title: '名字不能为空'
					})
					return
				}
				if (that.email.length == 0) {

					uni.showToast({
						title: '邮箱不能为空'
					})
					return
				}
				if (that.tel.length == 0) {

					uni.showToast({
						title: '手机号不能为空'
					})
					return
				}

				if (that.deptDisplay === '请选择') {

					uni.showToast({
						title: '请选择部门'
					})
					return
				}


				if (that.roleDisplay === '请选择') {
					uni.showToast({
						title: '请选择角色'
					})
					return
				}

				let role = '[' + that.roleRange[that.role].id + ']'
				let url
				if (that.isEdit) {
					url = that.url.updateEmployeeInfo
				} else {
					url = that.url.insertEmployee
				}
				that.ajax(url, 'POST', {
					dept_id: that.deptRange[that.dept].id,
					email: that.email,
					hiredate: that.hiredate,
					sex: that.sexDisplay,
					name: that.name,
					role: role,
					tel: that.tel,
					code: that.code
				}, function(resp) {

					if (that.isEdit) {

						uni.showModal({
							title: '修改成功',
							showCancel: false,
							content: `该员工的注册邀请码为${that.code}`,
							success: function(resp) {
								if (resp.confirm) {
									uni.navigateBack({});
								}
							}
						});
					} else {
						uni.showModal({
							title: '添加成功',
							showCancel: false,
							content: `该员工的注册邀请码为${resp.data.result.code}`,
							success: function(resp) {
								if (resp.confirm) {
									uni.navigateBack({});
								}
							}
						});

					}
				})
			}
		}
	}
</script>

<style lang="less">
	@import url('edit_employee_detail.less');
</style>
